// src/lib/payroll.ts
export type PayrollConfig = {
    year: number;
    workHoursMonthly: number; // e.g., 220
    insurance: { worker: number; employer: number; unemployment: number };
    allowances: { housing: number; food: number; childPerKid: number };
    tax: {
        exemptionMonthly: number; // معافیت ماهانه
        brackets: { upTo: number | null; rate: number }[]; // طبقات پلکانی (آستانه‌های تجمعی)
    };
};

export type PayrollInput = {
    mode: "daily" | "monthly" | "hourly";
    baseWage: number; // مزد پایه به ازای روز/ماه/ساعت بنا به mode
    days: number; // روزهای کارکرد ماه
    hoursOvertime: number; // ساعات اضافه‌کاری
    overtimeFactor: number; // ضریب اضافه‌کاری (مثلاً 1.4)
    married: boolean;
    childCount: number;
    includeHousing: boolean; // آیا حق مسکن لحاظ شود؟
    includeFood: boolean; // آیا بن کارگری لحاظ شود؟
    includeChildAllowance: boolean; // آیا حق اولاد لحاظ شود؟
    includeInsurance: boolean; // آیا بیمه محاسبه شود؟
    includeTax: boolean; // آیا مالیات محاسبه شود؟
    includeSeverance: boolean; // آیا سنوات ماهانه (توافقی) لحاظ شود؟
    severanceMonthly: number; // مبلغ سنوات ماهانه در صورت توافق (ریال)
};

export type PayrollBreakdown = {
    hourlyWage: number;
    baseMonthly: number; // پایه حقوق ماهانه (بدون مزایا)
    allowancesIncluded: {
        housing: number;
        food: number;
        child: number;
    };
    overtime: number;
    severanceMonthly: number;
    grossBeforeDeductions: number; // جمع دریافتی قبل از کسورات
    insuranceBase: number; // مبلغ مشمول بیمه
    insurance: {
        worker: number;
        employer: number;
        unemployment: number;
        total: number; // جمع کل سهم‌ها (برای اطلاع)
    };
    taxableIncome: number; // درآمد مشمول مالیات (پس از کسر بیمه کارگر و معافیت)
    tax: number;
    netPay: number; // خالص پرداختی (به کارگر)
    employerCost: number; // هزینه کل برای کارفرما
};

export function toMonthlyBase(
    mode: PayrollInput["mode"],
    baseWage: number,
    days: number,
    workHoursMonthly: number
): { baseMonthly: number; hourlyWage: number } {
    if (mode === "monthly") {
        const baseMonthly = baseWage;
        const hourlyWage = baseMonthly / workHoursMonthly;
        return { baseMonthly, hourlyWage };
    }
    if (mode === "daily") {
        const baseMonthly = baseWage * days;
        const hourlyWage = baseMonthly / workHoursMonthly;
        return { baseMonthly, hourlyWage };
    }
    // hourly
    const baseMonthly = baseWage * workHoursMonthly;
    const hourlyWage = baseWage;
    return { baseMonthly, hourlyWage };
}

/**
 * محاسبه‌ی مالیات پلکانی با درنظرگرفتن این‌که upTo آستانه‌ی تجمعی هر پله است.
 */
export function calcTaxProgressive(
    taxable: number,
    exemptionMonthly: number,
    brackets: { upTo: number | null; rate: number }[]
): number {
    let remaining = Math.max(0, taxable - exemptionMonthly);
    let tax = 0;
    let prevCap = 0; // مرز بالای پله قبلی (تجمعی)

    for (const b of brackets) {
        if (remaining <= 0) break;
        const cap = b.upTo ?? Number.POSITIVE_INFINITY; // پله آخر
        const width = Math.max(0, cap - prevCap);       // عرض واقعی پله
        const slice = Math.min(remaining, width);
        tax += slice * b.rate;
        remaining -= slice;
        prevCap = cap;
    }
    return Math.max(0, Math.floor(tax));
}

export function computePayroll(cfg: PayrollConfig, input: PayrollInput): PayrollBreakdown {
    const { baseMonthly, hourlyWage } = toMonthlyBase(
        input.mode,
        input.baseWage,
        input.days,
        cfg.workHoursMonthly
    );

    const allowances = {
        housing: input.includeHousing ? cfg.allowances.housing : 0,
        food: input.includeFood ? cfg.allowances.food : 0,
        child: input.includeChildAllowance
            ? cfg.allowances.childPerKid * (input.married ? input.childCount : 0)
            : 0,
    };

    const overtime =
        input.hoursOvertime > 0
            ? Math.floor(input.hoursOvertime * (hourlyWage) * input.overtimeFactor)
            : 0;

    const severanceMonthly = input.includeSeverance ? input.severanceMonthly : 0;

    const grossBeforeDeductions =
        baseMonthly +
        allowances.housing +
        allowances.food +
        allowances.child +
        overtime +
        severanceMonthly;

    // مبلغ مشمول بیمه: پایه + اقلام مشمول (در صورت نیاز سوییچ‌های جزئی‌تر اضافه کن)
    const insuranceBase = input.includeInsurance
        ? baseMonthly + allowances.housing + allowances.food + allowances.child + overtime
        : 0;

    const workerIns = Math.floor(insuranceBase * cfg.insurance.worker);
    const employerIns = Math.floor(insuranceBase * cfg.insurance.employer);
    const unempIns = Math.floor(insuranceBase * cfg.insurance.unemployment);

    const taxableIncomeBase = input.includeTax ? grossBeforeDeductions - workerIns : 0;
    const tax = input.includeTax
        ? calcTaxProgressive(taxableIncomeBase, cfg.tax.exemptionMonthly, cfg.tax.brackets)
        : 0;

    const netPay = grossBeforeDeductions - workerIns - tax;
    const employerCost = grossBeforeDeductions + employerIns + unempIns;

    return {
        hourlyWage: Math.floor(hourlyWage),
        baseMonthly: Math.floor(baseMonthly),
        allowancesIncluded: allowances,
        overtime,
        severanceMonthly,
        grossBeforeDeductions: Math.floor(grossBeforeDeductions),
        insuranceBase: Math.floor(insuranceBase),
        insurance: {
            worker: workerIns,
            employer: employerIns,
            unemployment: unempIns,
            total: workerIns + employerIns + unempIns,
        },
        taxableIncome: Math.max(0, Math.floor(taxableIncomeBase)),
        tax,
        netPay: Math.floor(netPay),
        employerCost: Math.floor(employerCost),
    };
}
